.popup {
  visibility: hidden;
  opacity: 0;
  transition: visibility .3s, opacity .3s ease;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
