.popup__container {
  position: relative;
  width: 358px;
  background-color: #FFFFFF;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  padding: 34px 36px 37px;
}

@media screen and (max-width: 700px) {
  .popup__container {
    padding: 25px 22px;
    width: 238px;
  }
}
